.container {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
  max-width: 300px;
}

.label {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 10px;
}
.input {
  margin-bottom: 10px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: none;
  /* max-width: 1000px; */
}
